.banner {
    color: white;
    height: 448px;
    object-fit: contain;
}

.banner-contents {
    height: 190px;
    margin-left: 30px;
    padding-top: 140px;
}

.banner-title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner-description {
    height: 80px;
    width: 45rem;
    max-width: 360px;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
}

.banner-button {
    cursor: pointer;
    color: white;
    background-color: rgba(51, 51, 51, 0.5);
    font-weight: 700;
    outline: none;
    border: none;
    border-radius: 0.2vw;
    margin-right: 1rem;
    padding: 0.5rem 2rem;
}

.banner-button:hover {
    color: black;
    background-color: lightgray;
    transition: all 0.2s;
}

.banner-bottom-fade {
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #111
    );
}